import React from 'react'
import Helmet from 'react-helmet'
import Link from 'gatsby-link'

const Project4 = () => (
  <div className="page Project4-page">
    <Helmet
      title="Tomcat-theme project, Kacper Cieciński Junior Web Developer"
      meta={[
        { name: 'description', content: 'Tomcat Theme rewritten, by Kacper Ciecinski' },
        { name: 'keywords', content: 'Kacper, Cieciński, Kacper Cieciński, Ciecinski, developer, junior developer,junior react developer, junior front-end developer,front-end,frontend,backend,junior backend developer,junior rails developer,React,Ruby on Rails,Javascript, HTML5, CSS, HTML theme, Tomcat theme' },
      ]}
    />

    <h1>Tomcat Theme</h1>
    <div className="row row-center">
      <div className="col-md-10">
      <div className="info-container">
      <p>Personal project, I rewritten Tomcat theme made by designing-world http://demo.designing-world.com/tomcat2/tomcat/index.html <br/>
         I made this to increse my skills in HTML5 and CSS, also improved my knowlage in making websites responsive and accesible <br/>
         I learned CSS Grid and some CSS3 animations
      </p>
        <p>
          <a target="_blank" href="https://kciecinski.github.io/tomcat-theme/"><i className="fa fa-th-large"></i>Live Demo</a>
          <a target="_blank" href="https://github.com/kciecinski/tomcat-theme"><i className="fa fa-github"></i>Github</a>
        </p>
      </div>
      <Link className="project-link" to="/projects">Back to projects</Link>

      </div>
    </div>

  </div>
)

export default Project4


